html,
body {
  background: rgba(240,232,247,1);
  /* background-color: #000; */
  
  margin: 0;
  padding: 0;
}
body {
  /* flex-direction: column;
  display: flex; */
}
button:focus {
  outline: 0;
}
#status {
  color: #f8f8f8;
  text-align: center;
  margin: 12px 0;
  font-weight: 300;
}
#pacman-container {
  height: 320px;
  padding: 0 0 80px;
}
#logo {
  transform-origin: top;
  image-rendering: pixelated;
  background: url(./images/bck2.png) 0 -5px no-repeat;
  background-color: #7260af;
  /* background-color: #000; */
  width: 554px;
  height: 186px;
  margin: 0 auto;
  position: relative;
  transform: scale(2.2);
}
#logo #pcm-c {
  border-top: none;
  margin: 0 auto;
  position: relative;
  top: 20px;
}
#logo-l {
  background: #990;
  width: 200px;
  height: 2px;
  display: none;
  position: absolute;
  top: 157px;
  left: 177px;
  overflow: hidden;
}
#logo-b {
  background: #ff0;
  width: 0;
  height: 8px;
  position: absolute;
  left: 0;
}
.controller-panels {
  flex-direction: row;
  margin: 9px auto 0;
  padding: 30px 0;
  display: flex;
}
.panel {
  box-sizing: border-box;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  display: flex;
}
.panel:first-child {
  border-right: 1px dashed #565656;
  width: 396px;
  padding: 0 22px 0 13px;
}
.panel:last-child {
  width: 353px;
  padding: 0 9px 0 22px;
}
.panel-row {
  flex-direction: row;
  display: flex;
}
.panel-cell {
  flex-direction: column;
  flex-grow: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}
.panel-cell-fill {
  flex-grow: 1;
}
.panel-cell p {
  color: #333;
  text-align: center;
  margin: 0;
  padding: 0;
  font-size: 10px;
}
.controller-panels button {
  box-sizing: border-box;
  cursor: pointer;
  background: 0 0;
  border: none;
  margin: 0;
  padding: 0;
}
#train-status {
  width: 124px;
}
.big-buttons {
  justify-content: space-between;
}
.big-buttons button {
  flex-direction: row;
  align-items: center;
  display: flex;
}
.big-buttons button span {
  color: #333;
  text-align: left;
  text-transform: uppercase;
  white-space: nowrap;
  border-top: 2px solid #484848;
  border-bottom: 2px solid #484848;
  padding: 9px 11px;
  font-size: 18px;
  font-weight: 500;
  display: inline-block;
}
.params-webcam-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 35px;
  display: flex;
}
.webcam-box-outer {
  box-sizing: border-box;
  background: #7260af;
  border: 1px solid rgba(240,232,247,1);
  border-radius: 4px;
  padding: 9px;
  display: inline-block;
}
.webcam-box-inner {
  box-sizing: border-box;
  border: 1px solid rgba(240,232,247,1);
  border-radius: 4px;
  justify-content: center;
  width: 160px;
  display: flex;
  overflow: hidden;
}
#webcam {
  height: 160px;
  transform: scaleX(-1);
}
.hyper-params {
  flex-direction: column;
  margin-left: 12px;
  display: flex;
}
.dropdown {
  flex-direction: column;
  width: 110px;
  margin-bottom: 10px;
}
.dropdown label {
  color: #333;
  font-size: 11px;
  font-weight: 300;
  line-height: 1;
  display: block;
}
.dropdown .select {
  position: relative;
}
.dropdown .select select {
  -webkit-appearance: none;
  -moz-appearance: none;
  /* color: #c9c9c9; */
  color: #666;
  background: 0 0;
  border: none;
  border-bottom: 1px solid #313131;
  border-radius: 0;
  outline: none;
  width: 100%;
  padding: 6px 0;
  font-size: 12px;
  display: block;
}
.dropdown .select:after {
  content: "arrow_drop_down";
  color: #999;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  pointer-events: none;
  font-family: Material Icons;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  display: inline-block;
  position: absolute;
  top: 6px;
  right: 0;
}
.joystick-panel {
  margin-top: 13px;
}
.panel-cell .help-text {
  text-align: left;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-style: italic;
  line-height: 1.1;
  position: absolute;
  top: 0;
  left: 0;
}
.panel-row-top .panel-cell-left {
  background: url(./images/pointer.svg) 98% 46%/38% no-repeat;
}
.panel-row-middle .panel-cell {
  height: 132px;
}
.thumb-box {
  display: inline-block;
}
.panel-row-middle .thumb-box {
  margin-top: 18px;
}
.thumb-box-outer {
  box-sizing: border-box;
  background: #7260af;
  border: 1px solid rgba(240,232,247,1);
  border-radius: 4px;
  padding: 9px;
  transition: box-shadow 0.3s;
  display: inline-block;
  position: relative;
}
[data-active="up"] .panel-row-top .thumb-box-outer,
[data-active="down"] .panel-row-bottom .thumb-box-outer,
[data-active="left"] .panel-cell-left .thumb-box-outer,
[data-active="right"] .panel-cell-right .thumb-box-outer {
  box-shadow: 0 0 4px 4px #fa0;
}
.thumb-box-inner {
  box-sizing: border-box;
  border: 1px solid rgba(240,232,247,1);
  background: rgba(240,232,247,1);
  border-radius: 4px;
  justify-content: center;
  width: 66px;
  display: flex;
  overflow: hidden;
}
.thumb {
  height: 66px;
  transform: scaleX(-1);
}
.thumb-box-outer .record-button {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.thumb-box-outer .record-button span {
  color: #f8f8f8;
  /* opacity: 0.5; */
  background: #7260af;
  border: 1px solid rgba(240,232,247,1);
  border-radius: 3px;
  font-size: 8px;
  display: block;
  position: absolute;
  bottom: 9px;
  left: 9px;
  right: 9px;
}
#copyright {
  color: #f8f8f8;
  text-align: center;
  margin: 12px 0;
  font-weight: 300;
}
#no-webcam {
  text-align: center;
  color: #fff;
  padding: 30px;
  font-size: 30px;
  line-height: 30px;
  display: none;
}
